        const {
            Last_name,
            First_name,
            Phone,
            Company,
            Department,
            Job_title,
            Email,
            Company_size,
            Email_verification,
            Phone_verificatio
        } = this.state;

        const content = 'https://www.digitimes.com.tw/seminar/ibm_20201116_v2/ajax/testapi/upload/';
        let re = /^[0-9]+$/;
        let emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
        
        function getState(_state) {
            return _state ? "NOT CHECKED" : "CHECKED"
        }

